.background {
    background-color: burlywood;
    display: flex;
}

.message {
    background: rgb(60, 55, 141);
    width: 20%;
    border-radius: 5px 5px 5px 0;
    margin-left: 0%;
    margin-top: 1%;
    margin-bottom: 1%;
    opacity: .8;
}

.text {
    padding: 2%;
    color: white;
}

.name {
    padding: 2%;
    color: black;
    font-weight: bold;
}

.time {
    margin-left: 80%;
    padding: 2%;
    color: white;
    opacity: .5;
}

.angle {
    width: 20px;
    height: 20px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 20px, rgb(60, 55, 141) 0);
    margin-right: 0;
    margin-top: 6.5%;
    opacity: .8;
}

.avatar {
    border-radius: 100%;
    width: 4%;
    height: 4%;
    margin-top: 4%;
    margin-left: 2%;
}
